.wrapper {
    max-width: 100%;
    min-width: 950px;
    /* display: flex;
    align-items: center;
    justify-content: space-between; */
    display: grid;
    grid-template-columns: 300px 35% 200px;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 20px 70px;
    background-color: #1B3764;
}

.head {
    width: 100%;
    display: flex;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    width: 40%;
    display: flex;
    align-items: baseline;
    justify-content: space-around;
}

.logo img:first-child {
    width: 35px;
    height: 30px;
}

.social-media {
    display: flex;
    width: 50%;
    justify-content: space-evenly;
}

.social-media-link {
    width: 40px;
    height: 40px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.social-media-link img {
    color: #1B3764;
    width: 15px;
    height: 15px;
}

.nav-wrapper {
    width: 100%;
    display: flex;
    justify-content: end;
}

.nav-wrapper a {
    font-family: Inter;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    color: #F6F8FC;
    text-decoration: none;
    margin: 0 20px;
    transition: color 1s;
}

.nav-wrapper .active {
    font-family: Inter;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    text-decoration: none;
    margin: 0 20px;
    transition: color 1s;
    color: #FFCA42;
}

.nav-wrapper a:hover {
    color: #FFCA42;
}

.order {
    width: 100%;
    height: 60px;
    background-color: #FFCA42;
    color: #1B3764;
    font-family: Cardo;
    font-size: 19px;
    font-weight: 400;
    line-height: 29px;
    letter-spacing: 0.5699999928474426px;
    transition: color 500ms;
    cursor: pointer;
}

.order:hover {
    color: #ff6842;
    text-decoration: underline;
}